# Layouts
--------------------------------------------------------------*/
.contactInfo {
	background-color: #2b3145;
	margin-top: 10rem;
	padding: 1rem 1rem 5rem 1rem;
}
.contactInfo .logo {
	margin: 0 auto;
	width: 70px;
	height: auto;
}
.contactInfo .contact {
	padding: 0 3rem;
}
.contactInfo .contact h3 {
	margin: 2rem 0 0 0;
	color: white;
}
.contactInfo .contact p {
	color: white;
}
.contactInfo .contact .socialIcons {
	display: flex;
	gap: 10px;
	justify-content: center;
}
.contactInfo .contact .socialIcons svg {
	width: 40px;
	height: auto;
	fill: white;
}
.contactInfo .contact .socialIcons svg:hover {
	fill: #24678d;
}
@media (min-width: 600px) {
	.contactInfo {
		display: flex;
		justify-content: center;
		padding: 1rem;
	}
	.contactInfo .logo {
		display: flex;
		min-width: 100px;
		align-items: center;
		margin: 0 1rem;
	}
	.contactInfo .contact {
		width: 600px;
	}
	.contactInfo .contact h3 {
		font-size: 2rem;
	}
}

.mobileScreen .menu-mobile-menu-container {
	position: relative;
	display: flex;
	margin: 0 auto;
	justify-content: center;
	border-radius: 50%;
}
.mobileScreen .menu-mobile-menu-container .menu {
	padding: 0 3rem 0 4rem;
	display: grid;
	grid-template-columns: minmax(auto, 50rem) minmax(auto, 50rem) minmax(auto, 50rem);
	justify-items: center;
	padding: 1rem 0;
	background-color: #2b3145;
	position: fixed;
	bottom: 1rem;
	width: 60%;
	margin: 0 auto;
	font-size: 1rem;
	z-index: 9999;
	border-radius: 3rem;
}
.mobileScreen .menu-mobile-menu-container li a svg {
	display: block;
	margin: 0 auto;
	text-align: center;
	fill: white;
}
.mobileScreen .menu-mobile-menu-container li a svg:hover {
	fill: #feae4b;
}
.mobileScreen .copyright {
	padding-left: 1rem;
}

.windowScreen {
	position: static;
}
.windowScreen .menu-social-container .menu,
.windowScreen .menu-single-home-container .menu,
.windowScreen .menu-return-work-menu-container .menu {
	display: none;
}
.windowScreen .copyright {
	display: none;
}

@media (min-width: 600px) {
	.mobileScreen {
		display: none;
	}

	.windowScreen {
		position: fixed;
		left: 0rem;
		top: 6rem;
		height: 80vh;
		width: 6%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
	}
	.windowScreen div ul li {
		margin-right: 15px;
		margin-left: 20px;
	}
	.windowScreen div ul li a:hover {
		text-decoration: line-through;
		transition: all 0.3s ease-in-out;
	}
	.windowScreen .menu-social-container,
.windowScreen .menu-single-home-container,
.windowScreen .menu-return-work-menu-container,
.windowScreen .copyright {
		flex: 0 auto;
		transform: rotate(-90deg);
	}
	.windowScreen .menu-social-container .menu,
.windowScreen .menu-single-home-container .menu,
.windowScreen .menu-return-work-menu-container .menu {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 300px;
	}
	.windowScreen .menu-social-container .menu::before,
.windowScreen .menu-single-home-container .menu::before,
.windowScreen .menu-return-work-menu-container .menu::before {
		background-color: #2b3145;
		content: "";
		height: 5rem;
		width: 1.15px;
		transform: rotate(-90deg);
		margin-right: 2rem;
	}
	.windowScreen .copyright {
		margin-bottom: 5rem;
		width: 200px;
		display: block;
	}
}
.site-header {
	height: 100px;
}
.site-header .site-branding {
	display: flex;
	margin-right: 0;
	margin-left: 0;
	justify-content: center;
	align-items: center;
}
.site-header .site-branding a:hover {
	transform: scale(1.1);
	transition: all 0.2s linear 0s;
}

