.post-13 .entry-header {
	display: none;
}
.post-13 .entry-content .intro {
	justify-content: center;
	margin: 0 auto;
	width: 80%;
	padding-top: 3rem;
}
.post-13 .entry-content .intro .wp-block-image {
	display: flex;
	justify-content: center;
}
.post-13 .entry-content .intro .wp-block-image img {
	width: 250px;
	height: 250px;
	border-radius: 50%;
	object-fit: cover;
	object-position: top center;
	transition: all 0.3s ease-in-out;
}
.post-13 .entry-content .intro h3 {
	text-align: center;
}
@media (min-width: 600px) {
	.post-13 .entry-content .intro .wp-block-image img {
		width: 300px;
		height: 300px;
	}
	.post-13 .entry-content .intro p {
		width: 60%;
		margin: 0 auto;
	}
}
.post-13 .entry-content .skills {
	padding-top: 5rem;
}
.post-13 .entry-content .skills .wp-container-4 {
	margin: 0 auto;
	padding: 2rem;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet {
	background-color: #2b3145;
	text-align: center;
	padding: 1rem;
	width: 300px;
	position: relative;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet h3 {
	margin: 1rem;
	color: white;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet li {
	color: white;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet:first-child {
	transform: translateX(-200px);
	opacity: 0;
	transition: 1s all ease;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet:nth-child(2) {
	padding-bottom: 3rem;
	transform: translateX(200px);
	opacity: 0;
	transition: 1s all ease;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet.active {
	transform: translateX(0);
	opacity: 1;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet:before, .post-13 .entry-content .skills .wp-container-4 .skillSet:after, .post-13 .entry-content .skills .wp-container-4 .skillSet > :first-child:before, .post-13 .entry-content .skills .wp-container-4 .skillSet > :first-child:after {
	position: absolute;
	content: " ";
	width: 50px;
	height: 50px;
	border-color: #feae4b;
	/* or whatever colour */
	border-style: solid;
	/* or whatever style */
}
.post-13 .entry-content .skills .wp-container-4 .skillSet:before {
	top: 0;
	left: 0;
	border-width: 5px 0 0 5px;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet:after {
	top: 0;
	right: 0;
	border-width: 5px 5px 0 0;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet > :first-child:before {
	bottom: 0;
	right: 0;
	border-width: 0 5px 5px 0;
}
.post-13 .entry-content .skills .wp-container-4 .skillSet > :first-child:after {
	bottom: 0;
	left: 0;
	border-width: 0 0 5px 5px;
}
.post-13 .entry-content .interest {
	padding: 3rem 0;
}
@media (min-width: 380px) {
	.post-13 .entry-content .skills .wp-container-4 {
		max-width: 300px;
	}
}
@media (min-width: 782px) {
	.post-13 .entry-content .skills .wp-container-4 {
		max-width: 600px;
	}
