.workIntro,
.aboutIntro {
	overflow: hidden;
	position: relative;
	height: 100vh;
	width: 100%;
}
.workIntro::after,
.aboutIntro::after {
	background-color: #092244;
	content: "";
	position: absolute;
	height: 5px;
	width: 100%;
	bottom: 0;
}
.workIntro .intro,
.aboutIntro .intro {
	position: absolute;
	top: 25%;
	margin-bottom: 5rem;
	overflow: hidden;
	height: auto;
}
.workIntro .intro h1, .workIntro .intro p,
.aboutIntro .intro h1,
.aboutIntro .intro p {
	transform: translateY(200px);
	animation: slideUp ease-in-out forwards 0.8s;
}
@keyframes slideUp {
	0% {
		transform: translateY(200px);
	}
	100% {
		transform: translateY(0px);
	}
}
.workIntro .intro h1,
.aboutIntro .intro h1 {
	font-size: 4rem;
	margin: 0;
}
.workIntro .intro p,
.aboutIntro .intro p {
	margin: 0;
}
.workIntro .seeWork,
.workIntro .seeAbout,
.aboutIntro .seeWork,
.aboutIntro .seeAbout {
	position: absolute;
	bottom: 30%;
	left: 50%;
	transform: translate(-50%, -50%);
}
.workIntro .seeWork span,
.workIntro .seeAbout span,
.aboutIntro .seeWork span,
.aboutIntro .seeAbout span {
	display: block;
	width: 30px;
	height: 30px;
	border-bottom: 2px solid #24678d;
	border-right: 2px solid #24678d;
	transform: rotate(45deg);
	margin: -10px;
	animation: animate 2s infinite;
}
.workIntro .seeWork span:nth-child(2),
.workIntro .seeAbout span:nth-child(2),
.aboutIntro .seeWork span:nth-child(2),
.aboutIntro .seeAbout span:nth-child(2) {
	animation-delay: -0.2s;
}
.workIntro .seeWork span:nth-child(3),
.workIntro .seeAbout span:nth-child(3),
.aboutIntro .seeWork span:nth-child(3),
.aboutIntro .seeAbout span:nth-child(3) {
	animation-delay: -0.4s;
}
@keyframes animate {
	0% {
		opacity: 0;
		transform: rotate(45deg) translate(-20px, -20px);
	}
	50% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		transform: rotate(45deg) translate(20px, 20px);
	}
}
@media (min-width: 600px) {
	.workIntro .intro,
.aboutIntro .intro {
		top: 30%;
	}
	.workIntro .intro h1,
.aboutIntro .intro h1 {
		font-size: 7rem;
	}
	.workIntro .seeWork,
.workIntro .seeAbout,
.aboutIntro .seeWork,
.aboutIntro .seeAbout {
		position: absolute;
		bottom: 25%;
	}
}

